.shortcut {
	display: flex;
	align-items: center;

	kbd {
		background-color: #eee;
		border-radius: 3px;
		border: 1px solid #b4b4b4;
		box-shadow: 0 1px 1px rgb(0 0 0 / 20%),
			0 2px 0 0 rgb(255 255 255 / 70%) inset;
		color: #333;
		display: inline-block;
		font-size: 0.85em;
		font-weight: 700;
		line-height: 1;
		padding: 2px 4px;
		white-space: nowrap;
	}

	span::before {
		content: ": ";
		margin-left: 5px;
	}

	span {
		font-weight: bold;
	}
}
