.message-popup {
	display: none;

	align-items: center;
	justify-content: center;

	position: absolute;
	top: 0;
	width: 100%;
	height: 100%;

	background-color: var(--message-popup-bg);

	font-family: "Press Start 2P", cursive;
	font-size: 10px;

	&.show {
		display: flex;
	}
}
